/* --------Search Input CSS--------- */
#home-search {
  border: 2px solid black;
  background-color: rgba(255, 235, 205, 0.564);
    background-position: right 10px center;
    background-repeat: no-repeat;
    font-size: 18px;
    color: whitesmoke;
    font-family: "Oswald", sans-serif;
    font-optical-sizing: auto;
    font-weight: weight;
    font-style: normal;
    box-shadow: 20px 20px 60px #00000048,inset -20px -20px 60px #ffffff5d;
    border-radius: 20px;

}
#home-search:focus {
  border-color: black;
  color: blue;
  box-shadow: 20px 20px 60px #00000048,inset -20px -20px 60px #ffffff5d;
  border-radius: 20px;
  transition: 1s;
}
@media (max-width: 600px) {
  #home-search {
      font-size: 18px;
      padding: 12px;
  }
}
#search-btn{
  width: 120px;
  background-color: blanchedalmond;
  border: 2px solid black;
  font-size: 30px;
  color: black;
  box-shadow: 20px 20px 60px #00000048,inset -20px -20px 60px #ffffff5d;
  border-radius: 20px;
}
#search-btn:hover{
  background-color: black;
  color: aqua;
}
