@media (max-width:590px) {
  .border.shadow table {
    width: 100%; 
    font-size: 12px; /* Reduce the font size */
  }

  .border.shadow th, .border.shadow td {
    padding: 5px; /* Reduce padding for better fit */
    text-align: center;
  }

  .border.shadow {
    overflow-x: auto; /* Horizontal scroll for small screens */
  }

  #ord-thead th {
    font-size: 10px; /* Further reduce header font size */
  }

  .border.shadow th, .border.shadow td {
    white-space: nowrap; /* Prevent wrapping inside table cells */
  }

  .border.shadow table {
    border-collapse: collapse;
  }
}
