* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: 'Arial', sans-serif;
}

body {
    background-color: #f9f9f9;
    color: #333;
    line-height: 1.6;
   
}

.privacy-container {
    max-width: 1200px;
    margin: 50px auto;
    padding: 20px;
    background-color: #fff;
    box-shadow: 0 4px 8px rgba(0,0,0,0.1);
    border-radius: 8px;
    margin-top: 110px;
}

.privacy-header {
    text-align: center;
    padding: 20px;
    border-bottom: 2px solid #ddd;
}

.privacy-header h1 {
    font-size: 2.5rem;
    color: #007BFF;
}

.privacy-header p {
    font-size: 1.1rem;
    color: #666;
}

.privacy-content {
    padding: 20px;
}

.privacy-content h2 {
    font-size: 1.8rem;
    color: #007BFF;
    margin-top: 20px;
}

.privacy-content p, .privacy-content ul {
    font-size: 1.1rem;
    color: #555;
    margin-bottom: 15px;
}

.privacy-content ul {
    list-style-type: disc;
    margin-left: 20px;
}
@media(max-width:345px) {
    ul a {
        font-size: 14px; /* Reduce link font size for smaller screens */
        color: blue; /* Ensure good contrast on mobile */
        word-wrap: break-word; /* Handle long email or phone numbers */
    }

    ul a:hover {
        color: darkblue; /* Change link hover color for mobile */
    }
}

