.cart-page {
    margin-top: 80px;
  }
  
  /* .cart-page h1 {
    padding: 15px !important;
    font-family: "roboto", sans-serif;
    font-weight: normal;
    background-color: rgba(0, 0, 255, 0.072) !important;
  }
  */
.cart-page p{
    margin-top: -10px;
  }
  .cart-page .cart-remove-btn {
    display: flex;
    align-items: center;
    justify-content: center;

  }
  .cart-page .cart-summary {
    text-align: center;
    padding-left: 100px;
   
  }

  #cart-details{
    background-color: cornsilk;
    border: 1px solid red;
    border-radius: 12px;
    margin-left: 40px;
  }
  #cart-details:hover{
    background-color: ghostwhite;
    box-shadow: 12px 12px 20px gainsboro;
  }
  .cart-img{
    height: 150px;
    width: 170px;
  }
  .detail{
    margin-left: 90px;
  }
  @media (max-width:849px) {
    #cart-details{
      display: block;
    }
   
  }
  @media (max-width:991px) {
    .cart-page {
      margin-top: 60px;
    }
  }
  @media (max-width:766px) {
    #cart-details{
      display: flex;
      width: 400px;
    }
    .cart-img{
      height: 50px;
      width: 100px;
      display: flex;
      align-items: center;
      justify-content: center;
    }
    .pay{
      padding: 50px;
    }
  }
  @media (max-width:459px) {
    #cart-details{
      display: flex;
      width: 320px;
      
    }
    .cart-img{
      height: 50px;
      width: 100px;
     
    }
    .detail{
      padding: 20px;
    }
    .cart-summary{
    padding: 70px;
    }
    .pay{
      padding: 10px;
    }
  }
  @media (max-width:370px) {
    #cart-details{
      display: flex;
      width: 200px;
      
    }
    .cart-img{
      height: 50px;
      width: 100px;
    }
    .detail{
      padding: 20px;
     
     margin-left: 0px;
    }
    .cart-summary{
    padding: 70px;
    }
    .pay{
      padding: 10px;
    }
  }
  @media (max-width:330px) {
    #cart-details{
      display: flex;
      width: 200px;
      
    }
    .cart-img{
      height: 50px;
      width: 100px;
    }
    .detail{
      padding: 20px;
     
     margin-left: 0px;
    }
    .cart-summary{
    padding: 40px;
    margin-left: -40px;
    }
    .pay{
      padding: 5px;
     
    }
  }