.product-details {
    margin-top: 120px;
  }
  #div-product{
    height: 420px;
    width: 370px;
    background-color: black;
    padding: 8px;
    position: relative;
    overflow: hidden;
    
  }
#product-img{
  height: 100%;
}
  #img-product{
    height: 400px;
    width: 350px;
    background-color: black;
    position: relative;
    z-index: 99;
  
  }
  #div-product::after{
    content: "";
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%,-50%) rotate(45deg);
    height: 600px;
    width: 200px;
    background-color: white;
    animation: move 1.5s infinite;
  }
  @keyframes move {
    100%{
      transform: translate(-50%,-50%) rotate(225deg);
    }
  }

  @media (max-width: 465px) {
   
    #div-product {
      height: 250px;
      width: 180px;
      padding: 4px;
      background-color: black;
      position: relative;
      overflow: hidden;
  }

  #img-product {
      height: 240px;
      width: 170px;
      background-color: black;
      position: relative;
      z-index: 99;
  }

  #div-product::after {
      content: "";
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%) rotate(45deg);
      height: 400px;
      width: 150px;
      background-color: white;
      animation: move 1.5s infinite;
  }
    #product-img {
      height: 100%;
      width: 100%;
      object-fit: cover;
    }

  }
  .product-details-info {
    display: flex;
    flex-direction: column;
    /* align-items: center; */
    justify-content: center;
    margin-left: 60px;
  }
  
  .product-details-info h6 {
    margin-bottom: 20px !important;
  }
  
  #atc-btn:hover{
    background-color: deepskyblue;
  }

  .similar-products .card {
    background-color: rgba(128, 128, 128, 0.097);
    width: 18rem;
  }
  
  .similar-products .card-name-price {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
  }
  
  .similar-products .card-price {
    color: green;
    font-weight: bold;
  }
  .similar-products button {
    border-radius: 0;
    width: 100%;
    border-top-left-radius: 5px;
    border-bottom-right-radius: 5px;
    font-size: 14px;
    font-weight: bold;
    background-color:cadetblue;
  }
  
  .similar-products .card-text {
    color: rgb(90, 89, 89);
  }
  
  .similar-products .card img:hover {
    transform: scale(0.9);
  }
  
  .similar-products .card-img-top {
    height: 300px !important;
  }
@media (max-width: 767px) {
 
  .product-details{
    display: block;
  }
    .product-details-info {
        margin-left: -40px;
        margin-top: 40px;
      
    }

    .product-details-info h6 {
        font-size: 14px;
    }

    #atc-btn {
        font-size: 12px;
        padding: 8px;
    }

    .similar-products .card {
        width: 100%;
        margin: 10px 0;
    }

    .similar-products .card-img-top {
        height: 200px !important;
    }

    .similar-products button {
        font-size: 12px;
        padding: 8px;
    }

    .similar-products .card-name-price {
        flex-direction: column;
        align-items: flex-start;
    }

    .similar-products .card-price {
        font-size: 14px;
        margin-top: 10px;
    }

}