/* ===================================
========= register page  */
.form-container {
  margin-top: 88px;
    display: flex;
    align-items: center;
    justify-content: center;
    min-height: 70vh;
    flex-direction: column;
    background-color: #ffdee9;
    background-image: linear-gradient(0deg, #ffdee9 0%, #b5fffc 100%);
  }
  
  .form-container form {
    width: 400px;
    box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
    padding: 20px;
    background-color: #fff;
  }
  
  .form-container form .title {
    text-align: center;
    margin-bottom: 15px;
    font-weight: bold;
    font-family: "Playfair Display", serif;
  }
  
  .form-container form input {
    border: none;
    border-bottom: 1px solid #000;
    border-radius: 0;
  }
  @media (max-width:991px) {
    .form-container {
      margin-top: 40px;
      
        display: flex;
        align-items: center;
        justify-content: center;
        min-height: 70vh;
        flex-direction: column;
        background-color: #ffdee9;
        background-image: linear-gradient(0deg, #ffdee9 0%, #b5fffc 100%);
      }
  }
  @media (max-width:538px) {
    .form-container form {
      width: 150px;
   
      box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
      padding: 20px;
      background-color: #fff;
    }
  }
  @media (max-width:420px) {
    .form-container form {
      width: 150px;
   
      box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
      padding: 20px;
      background-color: #fff;
    }
  }
  
  .login-btn {
    border: 1px solid black;
    border-radius: 0;
    background-color: gray;
    color: white;
    height: 40px;
    border-radius: 8px;
    
  }
  
  .login-btn:hover{
    color: white;
    background-color: blue;
     /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
  }
  
  .forgot-btn {
    border: 1px solid black;
    background-color: yellow;
    color: black;
    height: 40px;
    border-radius: 8px;
  }
  
  .forgot-btn:hover {
    color: white !important;
    background-color: red;
  }
  .register-btn{
    border: 1px solid black;
    border-radius: 0;
    color: white;
    height: 40px;
    border-radius: 8px;
    background: linear-gradient(to right, gray, black);
  }
  .register-btn:hover{
    color: white;
    background: linear-gradient(to right, rgb(133, 133, 244), rgb(11, 34, 207));
     /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
  }

